
import moment from 'moment'
import common from '@/libs/common.mixin'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [common],
components: {
Card,
Group,
Divider,
Flexbox,
FlexboxItem
},
data () {
return {
data: {}
}
},
mounted () {
this.init_data()
},
methods: {
orderStatus () {
let map_pay_status = {0:'未付款',1:'部分付款',2:'已付款'}


return map_pay_status[this.data.pay_status]
},
formatDate (time) {
return moment(time).format('YYYY-MM-DD')
},
getImg (row) {
let result = ''
if(row.lesson!=null){
result = row.lesson.lesson_cover_picture
}
else if(row.material!=null) {
result = row.material.image
}

return result||'/static/img/student/noimage.jpg'
},
getName (row) {
if(row.lesson!=null){
return row.lesson.lesson_name
}
else if(row.material!=null) {
return row.material.name
}
},
init_data () {
this.$rest('orders/'+this.$route.params.id)
.get()
.success(data=>{
this.data = data
}).error(response=>{
this.alert('失败',response.body.message)
})
}
}
}




